<template>
  <div class="search-bar-container">
    <b-form @submit.prevent="searchProperty">
      <div class="search-field-cintainer position-relative">
        <!-- TODO -->
        <nuxt-link
          class="slider-post-link d-md-none d-sm-block"
          :to="localePath('/search-details')"
        >
          <b-form-input
            class="banner-input"
            v-model="search"
            :placeholder="$t('home.searchField')"
          ></b-form-input>
        </nuxt-link>

        <b-form-input
          @click.stop="onInputSelect"
          ref="searchRef"
          class="banner-input d-none d-sm-none d-md-block"
          v-model="search"
          :placeholder="$t('home.searchField')"
          @input="displaySuggestions"
          autocomplete="off"
          @focus="handleShowList"
        ></b-form-input>

        <transition name="fade" mode="out-in">
          <ul v-if="showList" class="suggestions mb-0 p-0">
            <li
              @click.stop="handleSearch(item)"
              class="suggestion-link text-decoration-none"
              v-for="(item, index) in suggestions"
              :key="index"
            >
              {{ item.name }}
            </li>
          </ul>
        </transition>
      </div>

      <div class="cards-radio-container" v-if="show_SearchBtn_Term">
        <div class="cards-radio-item" v-on:click="term = 0">
          <div
            v-bind:class="term === 0 ? 'radio-circle checked' : 'radio-circle'"
          ></div>
          <span class="title">{{ $t('form.shortStay') }}</span>
          <span class="description">{{ $t('form.shortStayTooltip') }}</span>
        </div>
        <div class="cards-radio-item" v-on:click="term = 1">
          <div
            v-bind:class="term === 1 ? 'radio-circle checked' : 'radio-circle'"
          ></div>
          <span class="title">{{ $t('form.longStay') }}</span>
          <span class="description">{{ $t('form.longStayTooltip') }}</span>
        </div>
      </div>

      <AppButton
        v-if="(search.length < 1 && show_SearchBtn_Term)"
        class="banner-button"
        type="submit"
        variant="primary"
        :loading="loading"
        :disabled="search.length < 1"
        :label="$t('common.search')"
      >
      </AppButton>
    </b-form>
  </div>
</template>

<script>
import $axios from '~/helpers/axios-port'
import { createNamespacedHelpers } from 'vuex';
const { mapActions } = createNamespacedHelpers('SearchModule');

export default {
  name: 'SearchField',
  props: {
    show_SearchBtn_Term: {
      type: Boolean,
      default: true,
    }
  },
  data() {
    return {
      search: '',
      timeStamp: null,
      showList: false,
      term: 0,
      suggestions: [],
      loading: false,
      width: 0,
    }
  },
  methods: {
    ...mapActions(['setSearchLocationId', 'setSearchPropertyTerm']),
    handleSearch(item) {
      if(this.search.trim() != ''){
        this.setSearchLocationId(JSON.stringify(item.id))
        this.setSearchPropertyTerm(JSON.stringify(this.term))

        localStorage.setItem('searchLocationId', JSON.stringify(item.id))
        localStorage.setItem('searchPropertyTerm', JSON.stringify(this.term))

        this.$router.push({
          path: this.localePath('properties'),
          query: {
            location_id: this.$store.state.SearchModule.searchLocationId || localStorage.getItem('searchLocationId'),
            term: 0,
          },
        })

        this.gtag_report_conversion()
      }
    },
    searchProperty() {
      if(this.search.trim() != ''){
        this.gtag_report_conversion()
        this.$router.push({
          path: this.localePath('properties'),
          query: {
            keyword: this.search,
            term: this.term,
          },
        })
      }
    },
    gtag_report_conversion(url) {
      let callback = function () {
        if (typeof url != 'undefined') {
          window.location = url
        }
      }
      gtag('event', 'conversion', {
        send_to: 'AW-10826966991/bkSQCOy7j4wDEM_P2aoo',
        event_callback: callback,
      })
      return false
    },
    async onInputSelect() {
      var self = this
      self.loading = true;
      if(this.search.trim() != ''){
        await $axios.$get(`/autoComplete?keyword=${this.search}&term=${this.term}`)
          .then((res) => {
            self.suggestions = res.Data;
          })
      }
    },
    displaySuggestions() {
      if (!this.search || this.search == '') return
      this.onInputSelect()
    },
    handleShowList() {
      if (this.suggestions && this.suggestions.length > 0) {
        this.showList = true
      }
    },
    handleListVisibility() {
      this.showList = false
      if (this.$refs.searchRef) {
        this.$refs.searchRef.blur()
      }
    },
    handleResize() {
      if (window != undefined) this.width = window.innerWidth
    },
  },
  mounted() {
    document.addEventListener('click', () => {
      this.handleListVisibility()
    })
    if (window != undefined)
      window.addEventListener('resize', this.handleResize)
  },
  watch: {
    suggestions: {
      handler(value) {
        if (value && value.length > 0) {
          this.showList = true
          this.loading = false
        } else {
          this.showList = false
          this.loading = false
        }
      },
      immediate: true,
    },
  },
  destroyed() {
    if (window != undefined)
      window.removeEventListener('resize', this.handleResize)
  },
}
</script>

<style lang="scss">
.suggestions {
  background: #fff;
  border-radius: 0 0 5px 5px;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 999999 !important;
  overflow-y: scroll;
  height: 200px;
  box-shadow: 0px 0px #f3f3f3;
  border-radius: 20px !important;
  box-shadow: 0 10px 20px rgba(80, 48, 32, 0.16) !important;
  border-bottom-left-radius: 0 !important;
  border-bottom-right-radius: 0 !important;
  li {
    padding: 10px;
    border-bottom: 1px solid #f3f3f3;
    cursor: pointer;
    &:hover {
      background: #f3f3f3;
    }
    &:active {
      background: #f3f3f3;
    }
  }
}
.search-wrapper {
  .search-bar-container {
    transition: all 0.3s ease-in-out;
    position: relative;
    z-index: 50 !important;
    @include media-breakpoint-down(sm) {
      z-index: 999;
    }
  }

  .search-field-cintainer {
    position: relative;

    border-radius: 20px;
    box-shadow: 0 10px 20px rgba(80, 48, 32, 0.16) !important;
    width: 100%;
    height: 80px;
    padding: 7px 10px 7px 15px;
    background-color: #fff;
    outline: none !important;
    @include media-breakpoint-down(sm) {
      height: 60px;
    }
    .banner-input {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 20px;
      border: 1px solid #002bb9;
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      &::placeholder {
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
      }
      @include media-breakpoint-down(sm) {
        border: 0;
      }
      &:focus {
        box-shadow: none !important;
      }
    }
  }

  .banner-button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 12px 24px;
    width: 100%;
    height: 48px;
    background: #204ecf;
    border-radius: 8px;
    margin-top: 18px;
    @include media-breakpoint-down(sm) {
      border-radius: 0 5px 5px 0;
      font-size: 12px;
    }
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    [dir='rtl'] & {
      left: 0;
      right: auto;
      border-top-right-radius: 0 !important;
      border-bottom-right-radius: 0 !important;
      border-top-left-radius: inherit !important;
      border-bottom-left-radius: inherit !important;
    }
    svg {
      width: 20.4px;
      height: 20.4px;
      [dir='rtl'] & {
        transform: rotate(180deg);
      }
    }
    span {
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      color: #ffffff;
    }
  }
  .radio-field-cintainer {
    width: fit-content;
    margin: 0 auto;
    ::v-deep() .custom-control {
      display: inline-block;
      height: 42px;
      display: inline-block;
      margin-top: 5px;
      border-radius: 5px;
      overflow: hidden;
      position: relative;
      width: 80px;
      .custom-control-label {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        text-align: center;
        line-height: 42px;
        font-size: 13px;
        background-color: #fff;
        color: $mainColor;
        cursor: pointer;
      }
    }
    ::v-deep() .custom-control-input:checked ~ .custom-control-label {
      background-color: $mainColor !important;
      color: #fff;
    }
    ::v-deep() .custom-control-label::after,
    ::v-deep() .custom-radio .custom-control-label::before,
    ::v-deep() .custom-control-input:checked ~ .custom-control-label:before,
    ::v-deep()
      .custom-radio
      .custom-control-input:checked
      ~ .custom-control-label:after {
      display: none;
    }
  }
  ::placeholder {
    color: #a8a8a8;
    font-size: 12px;
    font-weight: bold;
    @include media-breakpoint-down(sm) {
      font-size: 9px !important;
      color: #4b4b4b;
    }
  }
  &__text-content {
    position: relative;
    z-index: 5;
  }
  .search-select {
    position: absolute;
    top: 0;
    right: 100px;
    bottom: 0;
    border: 0;
    outline: none !important;
    background-color: transparent !important;
    margin: 0 10px;
    padding: 0 10px;
    border-left: 1px solid #ccc;
    color: #bfb9b1;
    font-weight: bold;
    font-size: 14px;
    @include media-breakpoint-down(sm) {
      right: 90px;
      font-size: 10px;
      color: #4b4b4b;
      padding: 0 !important;
    }
    [dir='rtl'] & {
      left: 100px !important;
      right: initial !important;
      border-left: 0;
      border-right: 1px solid #ccc;
      @include media-breakpoint-down(sm) {
        right: initial !important;
        left: 90px !important;
        font-size: 10px;
      }
    }
  }
  &__sub-title {
    z-index: 5;
    position: absolute;
    bottom: 40px;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    h2 {
      color: white;
      font-weight: bold;
      @include media-breakpoint-down(sm) {
        font-size: 18px;
        text-align: center;
      }
    }
    a {
      margin-top: 20px;
      text-decoration: none;
      color: #fff;
      font-weight: bold;
      font-size: 18px;
      background-color: $primary;
      padding: 8px 20px;
      border-radius: 20px;
      text-transform: capitalize;
    }
  }
}
.app-header {
  .search-field-cintainer {
    height: 40px !important;
  }
  .banner-input {
    border-radius: 8px !important;
    border: 0 !important;
  }

  ::placeholder {
    font-size: 12px !important;
  }
}
.cards-radio-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 12px;
  margin-top: 15px;
  .cards-radio-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 12px;
    gap: 4px;
    width: 150px;
    height: 86px;
    border: 1px solid #111111;
    border-radius: 8px;
    filter: drop-shadow(0px 8px 24px rgba(129, 129, 129, 0.24));
    flex: none;
    order: 0;
    flex-grow: 1;
    .title {
      font-weight: 600;
      font-size: 12px;
      line-height: 12px;
      color: #111111;
    }
    .description {
      font-weight: 400;
      font-size: 12px;
      line-height: 12px;
      color: #48535B;
    }
    .radio-circle {
      background: #ffffff;
      border-radius: 50px;
      width: 24px;
      height: 24px;
      border: 1px solid #262d3d;
      &.checked {
        background-color: $primary;
        border: 2px solid white;
      }
    }
  }
}
</style>